@font-face {
  font-family: "PT Mono";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("PT Mono"), local("PT Mono"), url("../../fonts/PT Mono.woff2") format("woff2"); }

@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: bold;
  font-display: swap;
  src: local("Roboto Condensed"), local("Roboto Condensed-Bold"), url("../../fonts/RobotoCondensed-Bold.woff2") format("woff2"); }

@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: local("Roboto"), local("Roboto-Medium"), url("../../fonts/Roboto-Medium.woff2") format("woff2"); }

html {
  width: 100%;
  height: 110%;
  padding: 0;
  margin: 0;
  font-family: 'PT Mono', monospace; }

a {
  text-decoration: none; }

.wrapper {
  margin: 0 auto; }

.roboto-condensed {
  font-family: 'Roboto Condensed', sans-serif; }

.roboto {
  font-family: 'Roboto', sans-serif; }

.main-page {
  position: relative; }

.btn {
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 14px 25px 14px 25px;
  letter-spacing: 0.05em;
  font-size: 14px;
  line-height: 100%;
  text-transform: uppercase;
  border-radius: 5px; }

.btn--white {
  border: 1px solid #ffffff;
  color: #ffffff; }

.btn--blue {
  color: #ffffff;
  background-color: #00B2FF; }

.header {
  position: absolute;
  width: 100%;
  padding-top: 32px; }

.header__logo {
  display: flex;
  justify-content: center;
  width: 100%;
  max-width: 225px; }

.header__wrapper {
  display: flex;
  align-items: center;
  width: 100%;
  max-width: 1420px; }

.header__navigation {
  display: flex;
  margin-left: auto; }

.header__link {
  display: flex;
  align-items: center; }
  .header__link svg {
    margin-right: 15px; }
  .header__link span {
    font-size: 16px;
    line-height: 18px;
    color: #ffffff; }

.header__link--mail {
  margin-right: 90px; }

.header__link--phone {
  margin-right: 50px; }

.logo__link {
  display: flex;
  align-items: center; }

.logo__name {
  margin-left: 16px; }
  .logo__name span {
    display: block; }

.logo__title {
  color: #ffffff;
  font-size: 25px;
  line-height: 29px; }

.logo__subtitle {
  color: #788891;
  font-size: 12px;
  line-height: 12px; }

.banner {
  width: 100%;
  min-height: 880px;
  overflow-x: hidden;
  background: #1B2D37 url("../../img/banner-decor.svg") no-repeat center center; }

.banner__wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  min-height: inherit;
  max-width: 1405px;
  border: 1px solid transparent;
  background: url("../../img/retranigle-group.svg") no-repeat 48% center; }
  .banner__wrapper::before {
    content: "";
    position: absolute;
    top: 262px;
    right: -5%;
    display: block;
    width: 705px;
    height: 505px;
    background: url("../../img/banner-chip.png") no-repeat center center; }

.banner__content-container {
  position: relative;
  margin-top: 33px;
  padding-left: 98px; }
  .banner__content-container::before {
    content: "";
    position: absolute;
    top: 17px;
    left: 0;
    display: block;
    width: 66px;
    height: 2px;
    background-color: #ffffff; }

.banner__title {
  margin: 0;
  margin-top: 323px;
  font-size: 80px;
  line-height: 100%;
  color: #ffffff;
  letter-spacing: 0.07em;
  text-transform: uppercase; }

.banner__text {
  max-width: 683px;
  margin: 0;
  font-size: 22px;
  line-height: 34px;
  color: #ffffff; }

.banner__btn {
  padding: 19px 24px 19px 24px;
  margin-top: 42px;
  max-width: 407px;
  font-size: 16px;
  line-height: 19px;
  font-weight: bold;
  letter-spacing: 0.05em; }
